export class DramMapper {
  constructor(
    public setDram?: (enable: boolean) => void) {
  }
}

export class DramManager {
  constructor() {}

  public registerMapper(mapper: DramMapper): void {
    this.mappers.push(mapper);
  }

  public unregisterMapper(mapper: DramMapper): void {
    this.mappers = this.mappers.filter(obj => obj !== mapper);
  }

  public setDram(enable: boolean): void {
    for (let mapper of this.mappers) {
      mapper.setDram && mapper.setDram(enable);
    }
  }

  private mappers = new Array<DramMapper>();
}

